@import "ScssHelpers/helpers-all.scss";

.featured_resources {
}

.featured_resources__link {
	@include btn_style_gold_bordered;
	@include btn_size_normal;
}


.featured_resources__wrap {
	position: relative;
}

.featured_resources__button {
	position: absolute;
	top: 0;
	z-index: 3;

	&.featured_resources__button--prev_mod {
		left: 0;
		margin-left: -2rem;
	}
	&.featured_resources__button--next_mod {
		right: 0;
		margin-right: -2rem;
	}
	&.swiper-button-disabled {
		opacity: 0;
		pointer-events: none;
	}

	@include bp($point_2, min) {
		margin-top: 14%;
	}
	@include bp($point_3) {
		margin-top: 18%;
	}
}

.featured_resources__slider {
	&.swiper {
		overflow: visible;

		@include bp($point_2, min) {
			margin-right: -2.4rem;
		}
		@include bp($point_3) {
			margin-right: -1.5rem;
		}
	}
}

.featured_resources__list {
	@include flex_block(row, nowrap, flex-start, stretch);
}

.featured_resources__item {
	&.swiper-slide {
		height: auto;
	}

	@include bp($point_2, min) {
		padding-right: 2.4rem;
	}
	@include bp($point_3) {
		padding-right: 1.5rem;
	}

	@include bp($point_4, min) {
		width: 33.333%;
	}
	@include bp($point_4 - 1) {
		width: 69%;
	}
}
