@import "ScssHelpers/helpers-all.scss";

.article {
	@include text;

	@include bp($point_2, min) {
		padding-top: 12rem;
		padding-bottom: 14rem;
	}

	h1 {
		@include h2;
		letter-spacing: -0.018em;
		line-height: 1;
		color: $black;
	}

	h2 {
		@include h3;

		line-height: normal;
	}

	h3 {
		@include h4;
		letter-spacing: -0.011em;

		&:not(:last-child) {
			margin-bottom: 1.2rem;
		}
	}

	h5 {
		font-size: 2.4rem;
		line-height: 3.2rem;
		font-weight: 400;

		&:not(:last-child) {
			margin-bottom: 1.5rem;
		}
	}

	p:not(:last-child) {
		margin-bottom: 0.8rem;
	}

	figure {
		position: relative;

		&:not(:first-child) {
			margin-top: 4.6rem;
		}

		&:not(:last-child) {
			margin-bottom: 3.2rem;
		}

		a {
			position: absolute;
			top: calc(50% - 1.8rem);
			left: 50%;

			transform: translate(-50%, -62%);

			// margin-top: -8rem;
			// margin-left: -6rem;

			color: $white;

			&:hover {
				transform: translate(-50%, -62%) scale(1.1);
				color: $gold_2;
			}
			@include transition_all;
		}

		span {
			display: block;

			@include bp($point_2, min) {
				width: 12rem;
				height: 12rem;
			}
			@include bp($point_3) {
				width: 9.6rem;
				height: 9.6rem;
			}
		}
	}

	picture {
		position: relative;
		display: block;
		width: 100%;

		// padding-top: 51.6%;
		&:not(.product_gallery__thumbs_picture) {
			@include bp($point_4, min) {
				height: 38.4rem;
			}
			@include bp($point_4 - 1) {
				height: 24rem;
			}
		}

		&:not(:last-child) {
			margin-bottom: 1.8rem;
		}
	}

	img {
		position: absolute;
		top: 0;
		left: 0;

		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	figcaption {
		font-size: 1.4rem;
		letter-spacing: -0.01em;
	}

	ol {
		&:not(:first-child) {
			margin-top: 3.2rem;
		}

		&:not(:last-child) {
			margin-bottom: 4.4rem;
		}

		counter-reset: list;

		li {
			line-height: 1.375;
			display: flex;

			&:before {
				display: block;
				margin: 0 1.4rem 0 1rem;
				counter-increment: list;
				content: counter(list) ".";
			}

			&:not(:last-child) {
				margin-bottom: 1.2rem;
			}
		}
	}

	small {
		display: block;

		font-size: 1.2rem;
		line-height: 1.5;

		a {
			color: inherit;
			text-decoration: underline;

			&:hover,
			&:focus {
				text-decoration: none;
			}
		}

		&:not(:last-child) {
			margin-bottom: 0.8rem;
		}
	}

	time {
		&:not(:first-child) {
			&:before {
				content: "|";

				@include bp($point_4, min) {
					margin: 0 1rem 0 1rem;
				}

				@include bp($point_4 - 1) {
					margin: 0 0.4rem 0 0.4rem;
				}
			}
		}
	}

	address {
		font-style: normal;
	}

	&.section .divider {
		@include bp($point_2, min) {
			margin-top: 5.2rem;
			margin-bottom: 4.6rem;
		}
		@include bp($point_3) {
			margin-top: 4rem;
			margin-bottom: 3.8rem;
		}
	}
}

.article__in {
	width: 100%;
	max-width: 80.8rem;
	margin: 0 auto;
	padding: 0;
}

.article__head {
	span {
		display: block;
		font-weight: 500;
		letter-spacing: 0.19em;

		color: $gray_2;

		text-transform: uppercase;

		&:not(:last-child) {
			margin-bottom: 1.8rem;
		}

		@include bp($point_2, min) {
			font-size: 1.6rem;
		}
		@include bp($point_3) {
			font-size: 1.4rem;
		}
	}

	&:not(:last-child) {
		margin-bottom: 4.3rem;
	}
}

.article__wrap {
	&:not(:last-child) {
		margin-right: 2rem;
	}
}

//========================================
.article__meta {
	padding-bottom: 1.6rem;
	border-bottom: 1px solid $beige_2;

	color: $gold_2;
	@include text;

	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, center, center);
	}
	@include bp($point_3) {
		.article--griddle_mod & {
			display: flex;
			align-items: center;
		}
	}

	.article--griddle_mod & {
		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 5rem;
			}
			@include bp($point_3) {
				margin-bottom: 3.2rem;
			}
		}

		@include bp($point_2, min) {
			padding-bottom: 4.8rem;
		}
		@include bp($point_3) {
			padding-bottom: 3rem;
		}
	}

	a {
		@include spoiler_acc();
		margin-left: auto;
		white-space: nowrap;

		&:focus,
		&:hover {
			text-decoration: none;
		}

		&::before {
			height: 1px;
		}

		.article--griddle_mod & {
			margin-top: auto;
		}
	}

	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, center, center);
	}

	@include bp($point_3) {
		@include flex_block(row, wrap, flex-start, center, center);
	}
}
.article__meta_list {
	@include flex_block(row, nowrap, flex-start);
}

.article__meta_item {
	@include flex_block(row, nowrap, flex-start, center);

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-right: 3rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-right: 1.6rem;
		}
	}
}

.article__meta_icon {
	width: 3.2rem;
	height: 3.2rem;

	&:not(:last-child) {
		margin-right: 0.5rem;
	}
}
//========================================

.article__body {
	//padding-top: 2.9rem;
	padding-top: 4.2rem;
}

.article__row {
	width: 100%;

	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, flex-start, flex-start);
	}
}

.article__col_sidebar {
	@include bp($point_2, min) {
		width: 50.6%;
		padding-right: 2rem;

		.article--griddle_mod & {
			padding-right: 5rem;
		}
	}
	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 4rem;
		}
	}
}

.article__sidebar {
	h2 {
		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 3rem;
			}
			@include bp($point_3) {
				margin-bottom: 2rem;
			}
		}
	}

	h6 {
		@include h6;

		margin-bottom: 1.6rem;
		font-weight: 500;
	}

	ul:not(:last-child) {
		margin-bottom: 3.8rem;
	}

	li {
		@include text;

		&:not(:last-child) {
			margin-bottom: 1.1rem;
		}
	}
}

.article__block {
	&:not(:last-child) {
		margin-bottom: 4.4rem;
	}
}

.article__col_content {
	width: 100%;

	.article__meta & {
		@include flex_block(row, wrap, space-between, center);
	}

	@include bp($point_2, min) {
		.article--griddle_mod & {
			max-width: 66%;
		}
	}
}

.article__content {
	padding-top: 0.6rem;
	line-height: 1.5;

	h2 {
		&:not(:last-child) {
			margin-bottom: 1.8rem;
		}
	}

	strong {
		font-size: 2.2rem;
		line-height: 3.2rem;
		font-weight: 400;
	}

	ul {
		margin-top: 2.4rem;
		margin-bottom: 4.4rem;

		li {
			line-height: 1.375;
			display: flex;

			&:not(:last-child) {
				margin-bottom: 1.9rem;
			}

			&:before {
				display: block;
				flex-shrink: 0;

				width: 0.3em;
				height: 0.3em;
				margin: 1rem 1.8rem 0 1.2rem;

				background-color: $black;
				border-radius: 50%;
				content: "";
			}
		}
	}

	a {
		text-decoration: underline;
		color: inherit;
		cursor: pointer;

		&:focus,
		&:hover {
			text-decoration: none;
		}
	}
}

.article__slider {
	padding: 3.6rem 0 2.4rem;
}
