@import "ScssHelpers/helpers-all.scss";

.info_card {
	display: block;
	width: 100%;
	height: 100%;

	color: $black;

	&:hover,
	&:focus {
		text-decoration: none;
	}

	@include flex_block(column, nowrap, flex-start);
}

.info_card_img {
	position: relative;

	display: block;
	width: 100%;
	padding-bottom: 71%;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.7rem;
		}
		@include bp($point_3) {
			margin-bottom: 1.8rem;
		}
	}

	&::before {
		z-index: 1;

		content: "";
		background-color: $gold_2;
		opacity: 0;

		@include transition_opacity;
		@include stretch;
	}

	.info_card:hover &::before,
	.info_card:focus &::before {
		opacity: 0.77;
	}
}

.info_card_img__in {
	@include stretch;
	@include cover_img;
}

.info_card__content {
	flex: 1 1 auto;

	@include flex_block(column, nowrap, flex-start);
}

.info_card__subtitle {
	text-transform: uppercase;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 4px;
		}
		@include bp($point_3) {
			margin-bottom: 2px;
		}
	}

	@include h5;
}

.info_card__title {
	flex: 1 1 auto;

	line-height: 1.14;
	letter-spacing: -0.017em;

	&:not(:last-child) {
		margin-bottom: 1.5rem;
	}

	@include h4;
}

.info_card_link {
	opacity: 0;
	pointer-events: none;

	@include transition_opacity;
	@include flex_block(row, nowrap, flex-start, center, center);

	.info_card:hover &,
	.info_card:focus & {
		opacity: 1;
		pointer-events: initial;
	}
}

.info_card_link__text {
	color: $gold_2;
	font-size: 1.6rem;
	font-weight: 500;
	letter-spacing: -0.03em;

	border-bottom: 2px solid $gold_2;

	&:not(:last-child) {
		margin-right: 0.7rem;
	}
}

.info_card_link__icon {
	width: 0.6rem;
	height: 1.2rem;

	flex-shrink: 0;
	margin-top: 1px;

	color: $gold_2;
}
